.TeamPage{

   display: grid ; 
   grid-template-columns: 1fr 1fr 1fr 1fr;
   grid-template-rows: 1fr 1fr 1fr;
   column-gap: 10 px;
   row-gap: 10 px;

   .team-name-section{
       grid-column: span 3;
       justify-self: start;
       align-self: end;
   }
   .team-name-section{
       font-size: 2rem;
       margin-bottom: 20px;
   }
   .win-loss-section{
       justify-self: center;
       padding: 40px;
       text-align: center;
   }
   .match-detail-section{
       grid-column: span 4;
   }
   .more-link{
       align-self: center;
       justify-self: center;
       font-size: 1.5rem;
   }
}